"use client";

import { notFound, useParams } from "next/navigation";
import { motion } from "framer-motion";
import { Card, CardHeader, CardBody } from "@heroui/react";
import ReactMarkdown from "react-markdown";
import posts from "../postsData";

export default function BlogPost() {
  const params = useParams(); // <- safely unwrap params in a client component
  const post = posts.find((p) => p.slug === params?.slug);

  if (!post) {
    notFound();
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-primary/20 via-white/10 to-danger/20 px-6 py-20">
      <motion.div
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        className="max-w-4xl mx-auto"
      >
        <Card className="bg-white/10 backdrop-blur-xl border border-white/20 shadow-xl rounded-2xl overflow-hidden">
          <img
            src={post.image}
            alt={post.title}
            className="w-full h-96 object-cover"
          />
          <CardHeader className="p-6 text-4xl font-bold text-primary">
            {post.title}
          </CardHeader>
          <CardBody className="p-6 text-gray-100">
            <p className="text-sm text-gray-400 mb-6">{post.date}</p>

            {/* Render full content as Markdown */}
            <ReactMarkdown
              className="prose prose-invert max-w-none prose-headings:text-primary prose-a:text-danger hover:prose-a:underline"
            >
              {post.content}
            </ReactMarkdown>
          </CardBody>
        </Card>
      </motion.div>
    </main>
  );
}
